	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import forum from '@/views/forum/list'
	import shicaifenlei from '@/views/shicaifenlei/list'
	import yonghu from '@/views/yonghu/list'
	import discussyinshituijian from '@/views/discussyinshituijian/list'
	import yinshituijian from '@/views/yinshituijian/list'
	import yinshijilu from '@/views/yinshijilu/list'
	import discussshicaixinxi from '@/views/discussshicaixinxi/list'
	import shicaixinxi from '@/views/shicaixinxi/list'
	import liuyanfankui from '@/views/liuyanfankui/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/shicaifenlei',
			name: '食材分类',
			component: shicaifenlei
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/discussyinshituijian',
			name: '饮食推荐评论',
			component: discussyinshituijian
		}
		,{
			path: '/yinshituijian',
			name: '饮食推荐',
			component: yinshituijian
		}
		,{
			path: '/yinshijilu',
			name: '饮食记录',
			component: yinshijilu
		}
		,{
			path: '/discussshicaixinxi',
			name: '食材信息评论',
			component: discussshicaixinxi
		}
		,{
			path: '/shicaixinxi',
			name: '食材信息',
			component: shicaixinxi
		}
		,{
			path: '/liuyanfankui',
			name: '留言反馈',
			component: liuyanfankui
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
